.badge {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 30px;
    

    &--green {
        background-color: #42b883;
    }

    &--blue {
        background-color: #64C4ED;
    }

    &--pink {
        background-color: #FFBBCC;
    }

    &--grey{
        background-color: #C9D1D3;

    }
    &--lime{ background-color: #b6e6bd;}
    &--purple{ background-color: #c355f5;}
    &--black{ background-color: #110133;}
    &--red{ background-color: #ff6464;}

}