.tasks {
    $self: &;
    &:not(:last-of-type){
        margin-bottom: 30px;
    }
    &__title {
        font-family: 'Blinker';
        font-weight: bold;
        font-size: 32px;
        line-height: 39px;
        color: #64c4ed;
        padding-bottom: 20px;
        border-bottom: 1px solid #f2f2f2;

        &:hover {
            img {
                opacity: 0.2;
            }
        }

        img {
            opacity: 0;
            margin-left: 15px;
            cursor: pointer;

            &:hover {
                opacity: 0.8;
            }
        }

    }

    &__items {
        margin-top: 30px;

        h2{
            font-family: 'Blinker';
            font-weight: bold;
            font-size: 22px;
            color:#c9d1d3;
            text-align: center;
            position: absolute;
            left: 50%;
            top: 50%;
        }

        &-row {
            display: flex;
            align-items: center;
            height: 35px;

            input {
                margin-left: 15px;
                border: 0;
                font-size: 18px;
                width: 100%;
                color:purple
            }
            img{
                opacity:0;
                transition:opacity 0.15s ease-in-out;
            }

            #{$self}__edit-icon{
                margin-right: 5px;
            }
            #{$self}__remove-icon{
                width: 15px;
                height: 15  px;
            }


            &:hover{
                background-color: #f4f6f8;
                transition: background-color 0.15s ease-in-out;
                input{
                    background-color: #f4f6f8
                }
                img{
                    opacity:0.4
                }

                img:hover{
                    opacity:1;
                }
            }
        }
    }

    .checkbox {
        input:checked+label {
            background-color: #4DD599;
            border-color: #4DD599;

            svg {
                opacity: 1;

                path {
                    stroke: #fff;
                }
            }
        }

        input {
            display: none;
        }

        &:hover {
            label {
                background-color: #F2F2F2;
                border-color: #b2b2b2;

                svg {
                    opacity: 1;

                    path {
                        stroke: #b2b2b2
                    }
                }
            }
        }

        label {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 20px;
            height: 20px;
            border: 2px solid #e8e8e8;
            border-radius: 30px;
            cursor: pointer;

            svg {
                opacity: 0;

            }
        }


    }

    &__form{
        margin-top: 20px;
        &-new{
            display: flex;
            align-items: center;
            cursor: pointer;
            opacity: 0.4;
            transition: opacity 0.15s ease-in-out;
            &:hover{
                opacity: 0.8;
            }
            img{
                width: 16px;
                height: 16px;
                margin-left: 3px;
            }

            span{
                margin-left: 16px;
                font-size: 18px;
            }

        }
        &-adder{
            .button{
                margin-top: 15px;
                margin-right: 10px;
            }
        }
    }
}