.list {
    $self: &;
    margin-bottom: 30px;
    font-size:20px;
    &__remove-icon {
        opacity: 0;
        color: purple;
    }

    li {
        display: flex;
        align-items: center;
        cursor: pointer;
        padding: 10px 12px;
        margin-bottom: 5px;

        &:hover {
            background: rgba(255, 255, 255, 0.5);

            #{$self}__remove-icon {
                opacity: 0.2;
                &:hover{
                    opacity: 1;
                }
            }

            

        }

        &.active {
            background: #fff;
            box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.04);
            border-radius: 4px;
        }

        i {
            display: inline-flex;
            margin-right: 8px;

            svg {
                path {
                    fill: #7c7c7c
                }
            }

            .badge {
                position: relative;
                left: 3px;
                margin-right: 5px;
            }
        }

        span {
            flex: 1;
            text-overflow: ellipsis;
            overflow: hidden;
            width: 160px;
            white-space: nowrap;
            color:purple;
        }
        


    }

    &__add-button {
        position: relative;
        left: 3px;
        margin-right: 4px;
        opacity: .4;

        &:hover {
            opacity: 1;
        }

    }
}