:root {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  font-family: 'Blinker', cursive, -apple-system, system-ui, sans-serif;
  box-sizing: inherit;
}

.todo {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  width: calc(100vw - 30px);
  height: calc(100vh - 20px);
  transform: translate(-50%, -50%);
  box-shadow: 1px 2px 20px #f3f3f3;
  border-radius: 10px;
  border: 1px solid #f1f1f1;
  font-size: 14px;

  &__sidebar {
    background-color: #f4f6f8;
    width: 230px;
    height: calc(100%-120);
    border-right: 1px solid #f1f1f1;
    padding: 60px 20px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }

  &__tasks {
    flex: 1;
    padding: 60px; 
    overflow: auto;
  }


}

.button {
  background-color: #4dd599;
  border-radius: 4px;
  color: #fff;
  border: 0;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 14px;

  &:hover {
    background-color: darken($color: #4dd599, $amount: 7%)
  }

  &:disabled {
    background-color: #d8d8d8;
  }

  &:active {
    background-color: darken($color: #4dd599, $amount: 10%);
  }

  &--grey{ 
    background-color: #F4F6F8;
    color: #9C9C9C;

    &:hover{
      background-color: darken(#F4F6F8, 4%)};
      color: darken(#9C9C9C, 4%);
  }
}

.field {
  background: #fff;
  border: 1px solid #efefef;
  border-radius: 4px;
  padding: 8px 12px;
  width: 100%;
  font-size: 14px;

  &:focus {
    border-color: #dbdbdb;
  }

}